main {
  align-items: center;
  background-color: $graph-background;
  box-shadow: 1px 1px 3px 0px #747474;
  color: $text-color;
  display: flex;
  flex-direction: column;
  font-size: 1rem;
  max-width: 100%;
  padding: 1.5em 2em;

  h1 {
    font: {
      family: Arial, sans-serif;
      weight: lighter;
    }
    margin-top: 0;
    text-align: center;
  }
}

// bar-chart bar
.bar {
  fill: $bar-fill;

  &:hover {
    fill: $bar-fill--hover;
  }

  title{
    width: 10em;
  }
}
